import React from 'react';
import AppCard from '../../../../../@crema/core/AppCard';
import {
  Box,
  Divider,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from '@mui/material';
import {Fonts} from '../../../../../shared/constants/AppEnums';
import {useIntl} from 'react-intl';
import AppAnimate from '../../../../../@crema/core/AppAnimate';
import PropTypes from 'prop-types';

const AddClinetInformation = ({formData, setFormData}) => {
  const {messages} = useIntl();
  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value.trim(),
    });
  };

  return (
    <AppAnimate animation='transition.slideUpIn' delay={200}>
      <AppCard
        title={
          <Box fontSize={16} fontWeight={Fonts.BOLD}>
            {messages['sidebar.order.userInformation']}
          </Box>
        }
      >
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mt: 2,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['ClinetName']} </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='fullName'
              id='fullName'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['common.email']} </Box>
          <TextField
            onChange={(e) => handleChange(e)}
            name='email'
            type='email'
            id='email'
            variant='standard'
          />
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>
            {messages['common.phoneNumber']}{' '}
          </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='phone'
              type={'tel'}
              id='formatted-numberformat-input'
              variant='standard'
            />
          </Box>
        </Box>

        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['paymentMethod']} </Box>
          <Grid item textAlign={'start'} xs={6} sm={1.5}>
            <FormControl fullWidth>
              <InputLabel id='demo-simple-select-label'>
                {messages['payment']}
              </InputLabel>
              <Select
                labelId='demo-simple-select-label'
                id='payment'
                name='payedType'
                defaultValue={''}
                label={['payment']}
                onChange={(e) => handleChange(e)}
              >
                <MenuItem value={'CashOnDelivery'}>
                  {messages['CashOnDelivery']}{' '}
                </MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['OrderState']} : </Box>

          <Grid item textAlign={'start'} xs={6} sm={1.5}>
            <FormControl fullWidth>
              <InputLabel id='demo-simple-select-label'>
                {messages['State']}{' '}
              </InputLabel>
              <Select
                labelId='demo-simple-select-label'
                id='state'
                defaultValue={''}
                name='state'
                label={['State']}
                onChange={(e) => handleChange(e)}
              >
                <MenuItem value={'Pending'}>
                  {messages['common.pending']}
                </MenuItem>
                <MenuItem value={'InProgress'}>
                  {messages['InProgress']}
                </MenuItem>
                <MenuItem value={'InDelivery'}>
                  {messages['InDelivery']}
                </MenuItem>
                <MenuItem value={'Completed'}>{messages['Completed']}</MenuItem>
                <MenuItem value={'Canceled'}>{messages['Canceled']}</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Box>
        <Divider />

        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            my: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['common.notes']}: </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='notes'
              type={'email'}
              id='email'
              variant='standard'
            />
          </Box>
        </Box>
      </AppCard>
    </AppAnimate>
  );
};

export default AddClinetInformation;

AddClinetInformation.propTypes = {
  stat: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
};
